@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Poppins:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Courgette&family=Kaushan+Script&family=Poppins:wght@300;400;500;600;700;800&display=swap);
body {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bottom-nav a{cursor:pointer;transition:0.5s ease}.bottom-nav a:hover{background-color:#968984}.bottom-nav .MuiBottomNavigationAction-root.Mui-selected{color:rgba(0,0,0,0.54)}.bottom-nav .active{background-color:#968984}@media (max-width: 500px){.bottom-nav .MuiBottomNavigationAction-root.MuiBottomNavigationAction-iconOnly{padding-left:0;padding-right:0}}

.app .section{background-image:url(/LisnaHilmi/static/media/bg-wedding.b683eb0f.jpeg);background-size:cover;background-position:center;background-attachment:fixed;-webkit-animation:transitionOut 1s;animation:transitionOut 1s}.app .cover{height:100vh;width:100%;background:linear-gradient(rgba(39,39,39,0.7), rgba(39,39,39,0.7)),url(/LisnaHilmi/static/media/home.d2872587.jpeg);background-size:cover;background-attachment:fixed;background-position:center;-webkit-animation:transitionIn 1s;animation:transitionIn 1s}.app .audio{cursor:pointer;height:50px;width:50px;background-color:#fff;color:rgba(0,0,0,0.54);position:fixed;bottom:50%;right:1%;border-radius:100%;transition:0.5s ease}.app .audio:hover{background-color:#968984}@-webkit-keyframes transitionIn{from{opacity:0.5;-webkit-transform:translateY(-300px);transform:translateY(-300px)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes transitionIn{from{opacity:0.5;-webkit-transform:translateY(-300px);transform:translateY(-300px)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@-webkit-keyframes transitionOut{from{opacity:0.5}to{opacity:1}}@keyframes transitionOut{from{opacity:0.5}to{opacity:1}}@media (max-width: 500px){.app .cover{height:95vh}}

.couple{height:100vh}

.home{background-image:url(/LisnaHilmi/static/media/home.d2872587.jpeg);height:100vh;width:100%;background-size:cover;background-position:center;position:relative}.home .text{font-family:'Poppins', sans-serif;position:absolute;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);top:60%;left:50%;text-align:center;color:#fff}.home .text .invitation{font-size:14px;font-style:italic}.home .text .name{font-family:'Kaushan Script', cursive;font-size:48px;font-weight:500;padding:0}.home .text .date{font-size:14px;font-style:italic}@media (max-width: 500px){.home{height:90vh}}

.audio .icon{height:100%;display:flex;justify-content:center;align-items:center;font-size:24px}

.cover{font-family:'Poppins', sans-serif;position:relative}.cover .text{position:absolute;bottom:5%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#fff;text-align:center}.cover .text .invited{font-size:12px}.cover .text .name{font-size:32px;margin-top:10px;margin-bottom:10px}.cover .text .button{font-size:14px;cursor:pointer;padding:5px;border:2px solid white;border-radius:3px;width:60%;margin:auto;transition:0.5s ease}.cover .text .button:hover{background-color:grey}

